import React from 'react'
import 'antd/dist/antd.css';
import { Table, Input, Button, Space } from 'antd';
import Highlighter from 'react-highlight-words';
import { SearchOutlined } from '@ant-design/icons';
import { getSymbol ,getKlineByListTicker} from './utils';


export default class App extends React.Component {
  state = {
    filteredInfo: null,
    data:[],
    searchText: '',
    searchedColumn: '',
  };
  async componentDidMount(){
    const listTicker = await getSymbol()
    const data = await getKlineByListTicker(listTicker)
    this.setState({
      data:data
    });
  }
  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
          <Button
            type="link"
            size="small"
            onClick={() => {
              confirm({ closeDropdown: false });
              this.setState({
                searchText: selectedKeys[0],
                searchedColumn: dataIndex,
              });
            }}
          >
            Filter
          </Button>
        </Space>
      </div>
    ),
    filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase())
        : '',
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select(), 100);
      }
    },
    render: text =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text ? text.toString() : ''}
        />
      ) : (
        text
      ),
  });
  renderBackGroundColor(text,record,key){
    const {data} = this.state
    let color = parseFloat(text) > 0 ? "106, 168, 79" : "206, 71, 11"
    const highestValue = parseFloat(text) > 0 ? Math.max.apply(Math, data.map(function(o) { return o[key]; })):Math.min.apply(Math, data.map(function(o) { return o[key]; }))
    let alpha =record[key] / highestValue
    return {
      props: {
        style: { background: 'rgba('+color+','+alpha+')' }
      },
      children: <div>{text?text+"%":''}</div>
    };
  }

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };
  handleChange = (pagination, filters, sorter) => {
    console.log('Various parameters', pagination, filters, sorter);
    this.setState({
      filteredInfo: filters,
    });
  };
  handleDeleteRow = (record) =>{
    const filter = this.state.data.filter(e=>{
      return e.symbol!==record.symbol
    })
    this.setState({
      data:filter
    })
  }
  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
    });
  };


  render() {
    let {  filteredInfo, data } = this.state;
    filteredInfo = filteredInfo || {};
    const columns = [
      {
        title: 'Symbol',
        dataIndex: 'symbol',
        key: 'symbol',
        align:'center',
        ...this.getColumnSearchProps('symbol'),
      },
      {
        title: 'Now',
        dataIndex: 'price',
        key: 'price',
        align:'center',
        sorter: {
          compare: (a, b) => a.price - b.price,
          multiple: 1,
        },
        ellipsis: true,
      },
      {
        title: 'ATH',
        dataIndex: 'ath',
        key: 'ath',
        align:'center',
        sorter: (a, b) => a.ath - b.ath,
        ellipsis: true,
        sorter: {
          compare: (a, b) => a.ath - b.ath,
          multiple: 2,
        },
      },
      {
        title: 'Drop',
        dataIndex: 'drop',
        key: 'drop',
        align:'center',
        sorter: {
          compare: (a, b) => a.drop - b.drop,
          multiple: 3,
        },
        ellipsis: true,
        render:(text, record)=> this.renderBackGroundColor(text,record,"drop"),
      },
      {
        title: 'Aug L',
        dataIndex: '2021-08 L',
        key: '2021-08 L',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-08 L'] - b['2021-08 L'],
          multiple: 4,
        },
        ellipsis: true,
      },
      {
        title: 'Bounce',
        dataIndex: '2021-08 L Bounce',
        key: '2021-08 L Bounce',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-08 L Bounce'] - b['2021-08 L Bounce'],
          multiple: 5,
        },
        ellipsis: true,
        render:(text, record)=> this.renderBackGroundColor(text,record,"2021-08 L Bounce"),
      },
      {
        title: 'Jul L',
        dataIndex: '2021-07 L',
        key: '2021-07 L',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-07 L'] - b['2021-07 L'],
          multiple: 6,
        },
        ellipsis: true,
      },
      {
        title: 'Bounce',
        dataIndex: '2021-07 L Bounce',
        key: '2021-07 L Bounce',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-07 L Bounce'] - b['2021-07 L Bounce'],
          multiple: 7,
        },
        ellipsis: true,
        render:(text, record)=> this.renderBackGroundColor(text,record,"2021-07 L Bounce"),
      },
      {
        title: 'Jun L',
        dataIndex: '2021-06 L',
        key: '2021-06 L',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-06 L'] - b['2021-06 L'],
          multiple: 8,
        },
        ellipsis: true,
      },
      {
        title: 'Bounce',
        dataIndex: '2021-06 L Bounce',
        key: '2021-06 L Bounce',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-06 L Bounce'] - b['2021-06 L Bounce'],
          multiple: 9,
        },
        ellipsis: true,
        render:(text, record)=> this.renderBackGroundColor(text,record,"2021-06 L Bounce"),
      },
      {
        title: 'May L',
        dataIndex: '2021-05 L',
        key: '2021-05 L',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-05 L'] - b['2021-05 L'],
          multiple: 10,
        },
        ellipsis: true,
      },
      {
        title: 'Bounce',
        dataIndex: '2021-05 L Bounce',
        key: '2021-05 L Bounce',
        align:'center',
        sorter: {
          compare: (a, b) => a['2021-05 L Bounce'] - b['2021-05 L Bounce'],
          multiple: 11,
        },
        ellipsis: true,
        render:(text, record)=>  this.renderBackGroundColor(text,record,"2021-05 L Bounce"),
      },
      {
        title: 'Lowest',
        dataIndex: 'lowest',
        key: 'lowest',
        align:'center',
        sorter: {
          compare: (a, b) => a['lowest'] - b['lowest'],
          multiple: 12,
        },
        ellipsis: true,
      },
      {
        title: 'Up',
        dataIndex: 'up',
        key: 'up',
        align:'center',
        sorter: {
          compare: (a, b) => a['up'] - b['up'],
          multiple: 13,
        },
        ellipsis: true,
        render:(text, record)=>  this.renderBackGroundColor(text,record,"up"),
      },
      {
        title: 'Action',
        key: 'action',
        align:'center',
        render: (text, record) => (
          <Space size="middle">
            <a onClick={()=>{
              this.handleDeleteRow(record)
            }}>Delete</a>
          </Space>
        ),
      },
    ];
    return (
      <>
        <Space style={{ marginBottom: 16 }}>
          {/* <Button onClick={this.setAgeSort}>Sort age</Button>
          <Button onClick={this.clearFilters}>Clear filters</Button>
          <Button onClick={this.clearAll}>Clear filters and sorters</Button> */}
        </Space>
        <Table rowKey="symbol" size="small" style={{ margin: 24 }} pagination={false}  columns={columns} dataSource={data} onChange={this.handleChange} />
      </>
    );
  }
}